import React from "react";
import PropTypes from "prop-types";
import type { BestBet } from "../../types/FBresponse";

type BestBetDetails = {
  details: BestBet;
};

/**
 * Render a Funnelback best bet as a results card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__BestBet(props: BestBetDetails) {
  return (
    <li className="card card--bestbet">
      <a href={props.details.displayUrl} className="card__anchor card__details">
        <div className="card__details__text">
          <h3 className="card__heading">{props.details.titleHtml}</h3>
          <p className="card__description">{props.details.descriptionHtml}</p>
        </div>
      </a>
    </li>
  );
}

Finder__Results__BestBet.propTypes = {
  details: PropTypes.object,
};

export default Finder__Results__BestBet;
