import BestBet from "../cards/finder__results__bestbet";
import Card from "../cards/finder__results__card";
import DidYouMean from "./finder__didyoumean";
import Pagination from "./finder__pagination";
import PromoCard from "../cards/finder__results__promo";
import PropTypes from "prop-types";
import React from "react";
import Summary from "./finder__results__summary";
import Announcement from "./finder__announcement";
import type { FinderResults } from "../../types/results/results";

/**
 * Render Funnelback results.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results(props: FinderResults) {
  const updating = (
    <p className="finder__results__updating" aria-live="polite">
      <span className="fas fa-spinner fa-pulse icon" aria-hidden="true" />{" "}
      <span>Updating {props.summariseAs && props.summariseAs.plural}…</span>
    </p>
  );

  /**
   * Display promo card based on user search/filter input.
   *
   * Props passed from finder. If conditions are met, promo card will display between first
   * result card and the rest of the returned results.
   */
  let promoCardConditionMet = false, // Are conditions met to render promo card?
    matchedPromoGroup: { [key: string]: string }[] = [], // Matching promo group based on query parameters
    matchedPromoGroupDetails; // Matched promo group data to render to card

  if (props.promo) {
    const promoConditions = props.promo[0]["conditions"], // Conditions to render promo card
      promoDetails = props.promo[0]["details"], // Details rendered to promo card
      filterFacets = props.query.facets, // User-defined filter/search values
      searchFacets = props.query.query.toLowerCase(), // User-input search value
      queryFacetsAll = { ...filterFacets, searchFacets }; // Combine filter and search inputs

      let promoCardConditionGroup: string; // Group-specific data to render in promo card

    // Promo condition check and define group
    const queryFacetsArray = [];
    let queries = Object.values(queryFacetsAll);
    for (const query of queries) {
      queryFacetsArray.push(query);
    }

    // Remove empty array items
    queries = queries.filter((item) => item);

    // Loop query parameters and check if they match any of the promo card conditions
    for (const q of queries) {
      matchedPromoGroup = promoConditions.filter(
        (el) =>
          el.school === q ||
          el.level === q ||
          el.related === q ||
          el.subject === q ||
          el.type === q ||
          el.searchQuery1 === q,
      );
    }

    // Assign promo group value and define if promo card conditions are met
    if (matchedPromoGroup.length > 0) {
      promoCardConditionGroup = matchedPromoGroup[0]["promoGroup"];
      promoCardConditionMet = true;
    } else {
      promoCardConditionMet = false;
    }

    // Filter promo card details to match correct group of data
    matchedPromoGroupDetails = promoDetails.filter(
      (el) => el.promoGroup === promoCardConditionGroup,
    );
  }

  const resultsClass =
    "resultsVariant" in props.config
      ? `finder__results__list finder__results__list--${props.config.resultsVariant}`
      : "finder__results__list";

  if (props.response) {
    const summary = (
      <Summary
        clear={props.clear}
        config={props.config}
        currEnd={props.response.summary.currEnd}
        currStart={props.response.summary.currStart}
        numRanks={props.response.summary.numRanks}
        query={props.query}
        summariseAs={props.summariseAs}
        totalMatching={props.response.summary.totalMatching}
        update={props.update}
        summaryHeadingRef={props.summaryHeadingRef}
      />
    );

    const announcement = props.config.announcement && (
      <Announcement announcement={props.config.announcement} />
    );

    const didYouMean = (props.query.misspelling || props.response.spell) && (
      <DidYouMean
        query={props.query}
        summariseAs={props.summariseAs}
        response={props.response}
        update={props.update}
      />
    );

    // if we have more results than will fit on a single page, we need
    // pagination
    const pagination = props.response.summary.totalMatching &&
      props.response.summary.numRanks &&
      props.response.summary.totalMatching >
        props.response.summary.numRanks && (
        <Pagination
          currStart={props.response.summary.currStart}
          numRanks={props.response.summary.numRanks}
          query={props.query}
          totalMatching={props.response.summary.totalMatching}
          update={props.update}
        />
      );

    const promoCard = matchedPromoGroupDetails && (
      <PromoCard
        heading={matchedPromoGroupDetails[0]["heading"]}
        body={matchedPromoGroupDetails[0]["body"]}
        linkRef={matchedPromoGroupDetails[0]["linkRef"]}
        linkText={matchedPromoGroupDetails[0]["linkText"]}
      />
    );

    let results;
    const secondItemOnwards = props.response.results;
      

    if (
      promoCardConditionMet &&
      ((props.response.bestBets && props.response.bestBets.length > 0) ||
        props.response.results.length > 0)
    ) {
      if (props.query.startRank === 1) {
        const firstItemData = props.response.results[0];
        const secondItemOnwardsData = Array.from(secondItemOnwards.slice(1));

        results = (
          <ol start={props.response.summary.currStart} className={resultsClass}>
            <Card
              bestBets={false}
              details={firstItemData}
              type={props.type}
              query={props.query}
            />
            {promoCard}
            {secondItemOnwardsData.map((s) => (
              <Card
                bestBets={false}
                details={s}
                key={s.docNum}
                type={props.type}
                query={props.query}
              />
            ))}
          </ol>
        );
      } else {
        results = (
          <ol start={props.response.summary.currStart} className={resultsClass}>
            {props.response.results.map((card) => (
              <Card
                bestBets={false}
                details={card}
                key={card.docNum}
                type={props.type}
                query={props.query}
              />
            ))}
          </ol>
        );
      }
    }

    if (props.response.results && !promoCardConditionMet) {
      results =
        (props.response.bestBets && props.response.bestBets.length > 0) ||
        props.response.results.length > 0 ? (
          <ol start={props.response.summary.currStart} className={resultsClass}>
            {props.response.bestBets &&
              props.response.bestBets.map((card, i) => (
                <BestBet details={card} key={`best-bet__${i}`} />
              ))}

            {props.response.results.map((card) => (
              <Card
                bestBets={false}
                details={card}
                key={card.docNum}
                type={props.type}
                query={props.query}
              />
            ))}
          </ol>
        ) : null;
    }

    // render either the results, or a spinner while we wait for Funnelback
    const resultsContent = props.updating ? (
      <React.Fragment>{updating}</React.Fragment>
    ) : (
      <React.Fragment>
        {didYouMean}
        {announcement}
        {summary}
        {results}
        {pagination}
      </React.Fragment>
    );
    return <div className="finder__results">{resultsContent}</div>;
  } else {
    return <div className="finder__results">{updating}</div>;
  }
}

Finder__Results.propTypes = {
  clear: PropTypes.func,
  query: PropTypes.object,
  response: PropTypes.object,
  summariseAs: PropTypes.object,
  type: PropTypes.string,
  update: PropTypes.object,
  updating: PropTypes.bool,
  config: PropTypes.object,
  promo: PropTypes.array,
  pastEventsQuery: PropTypes.object,
  pastEventsResponse: PropTypes.object,
  pastEventsUpdate: PropTypes.object,
  matrixState: PropTypes.object,
};

export default Finder__Results;
