import React from "react";
import PropTypes from "prop-types";
import { formatReactDate, toBool } from "../../util";
import Finder__ToolTip from "../tools/finder__toolTip";
import type { ResultsNews } from "../../types/cards/card";

/**
 * Render a Funnelback result as a generic card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__News(props: ResultsNews) {
  const thumbnailSourceName =
      (props.details.listMetadata &&
        props.details.listMetadata.thumbnailSourceName &&
        props.details.listMetadata.thumbnailSourceName[0]) ||
      false,
    thumbnailSourceUrl =
      (props.details.listMetadata &&
        props.details.listMetadata.thumbnailSourceUrl &&
        props.details.listMetadata.thumbnailSourceUrl[0]) ||
      false,
    thumbnailAuthorName =
      (props.details.listMetadata &&
        props.details.listMetadata.thumbnailAuthorName &&
        props.details.listMetadata.thumbnailAuthorName[0]) ||
      false,
    thumbnailAuthorUrl =
      (props.details.listMetadata &&
        props.details.listMetadata.thumbnailAuthorUrl &&
        props.details.listMetadata.thumbnailAuthorUrl[0]) ||
      false,
    thumbnailLicenceType =
      (props.details.listMetadata &&
        props.details.listMetadata.thumbnailLicenceType &&
        props.details.listMetadata.thumbnailLicenceType[0]) ||
      false,
    thumbnailLicenceUrl =
      (props.details.listMetadata &&
        props.details.listMetadata.thumbnailLicenceUrl &&
        props.details.listMetadata.thumbnailLicenceUrl[0]) ||
      false,
    displayToolTip =
      props.details.listMetadata &&
      props.details.listMetadata.thumbnailToolTip &&
      toBool(props.details.listMetadata.thumbnailToolTip[0]);

  const formattedDate =
      props.details.listMetadata &&
      props.details.listMetadata.d &&
      formatReactDate(new Date(props.details.listMetadata.d[0])),
    dateString = props.details.listMetadata && props.details.listMetadata.d && (
      <p className="card__type">{formattedDate}</p>
    ),
    hashtags = props.details.listMetadata &&
      props.details.listMetadata.hashtagtext && (
        <p className="card__tag">
          <span className="fad fa-fw fa-hashtag icon" aria-hidden="true" />{" "}
          <span>
            Hashtag
            {props.details.listMetadata.hashtagtext.length > 1 && "s"}:
          </span>{" "}
          <span>{props.details.listMetadata.hashtagtext.join(", ")}</span>
        </p>
      ),
    thumbnail =
      props.details.listMetadata &&
      props.details.listMetadata.thumbnailTiny &&
      props.details.listMetadata.thumbnailTiny[0] &&
      props.details.listMetadata.thumbnailMax &&
      props.details.listMetadata.thumbnailMax[0] &&
      props.details.listMetadata.thumbnailMobile &&
      props.details.listMetadata.thumbnailMobile[0] ? (
        <picture>
          <source
            srcSet={props.details.listMetadata.thumbnailTiny[0]}
            media="(max-width: 375px)"
          />
          <source
            srcSet={props.details.listMetadata.thumbnailMax[0]}
            media="(min-width: 3840px)"
          />
          <img
            src={props.details.listMetadata.thumbnailMobile[0]}
            alt={props.details.title}
          />
        </picture>
      ) : props.details.listMetadata && props.details.listMetadata.thumbnail ? (
        <img
          src={props.details.listMetadata.thumbnail[0]}
          alt=""
          className="card__thumbnail"
        />
      ) : null;

  return (
    <li className="card card--news">
      <Finder__ToolTip
        thumbnailSourceName={thumbnailSourceName}
        thumbnailSourceUrl={thumbnailSourceUrl}
        thumbnailAuthorName={thumbnailAuthorName}
        thumbnailAuthorUrl={thumbnailAuthorUrl}
        thumbnailLicenceType={thumbnailLicenceType}
        thumbnailLicenceUrl={thumbnailLicenceUrl}
        displayToolTip={displayToolTip}
      />
      <a href={props.details.liveUrl} className="card__anchor card__details">
        {thumbnail}
        <div className="card__details__text">
          {dateString}
          <h3 className="card__heading">{props.details.title}</h3>
          <p className="card__description">
            {props.details.listMetadata &&
              props.details.listMetadata.c &&
              props.details.listMetadata.c[0]}
          </p>
          {hashtags}
        </div>
      </a>
    </li>
  );
}

Finder__Results__News.propTypes = {
  details: PropTypes.object,
};

export default Finder__Results__News;
