import React from "react";
import PropTypes from "prop-types";
import type { DidYouMean } from "../../types/results/results";

/**
 * Returns a Funnelback spelling suggestion as a button to update the query.
 *
 * @param {object} props React props.
 * @returns {object} - React component.
 */
function Finder__DidYouMean(props: DidYouMean) {
  const suggestion = props.response.spell?.text?.split(/\s/)[0].trim() ?? "";

  const didyoumean = props.query.misspelling ? (
    <p className="finder__results__didyoumean">
      No {props.summariseAs && props.summariseAs.plural} found for “
      {props.query.misspelling}”. Searching instead for “{props.query.query}”.
    </p>
  ) : (
    <p className="finder__results__didyoumean">
      Did you mean{" "}
      <button
        type="button"
        className="finder__didyoumean__button"
        onClick={() => {
          const newQuery = { ...props.query }; // clone the query object
          newQuery.query = suggestion;
          newQuery.startRank = 1;
          newQuery.misspelling = null;
          newQuery.interacted = true;
          newQuery.facets = {};
          props.update.query(newQuery);
          props.update.results(!props.update.updateState);
        }}
      >
        “{suggestion}”
      </button>
      ?
    </p>
  );

  return didyoumean;
}

Finder__DidYouMean.propTypes = {
  query: PropTypes.object,
  response: PropTypes.object,
  summariseAs: PropTypes.object,
  update: PropTypes.object,
};

export default Finder__DidYouMean;
