import React from "react";
import PropTypes from "prop-types";
import DOMPurify from "dompurify";
import type { Announcement } from "../../types/results/results";

/**
 * Renders the announcement box
 *
 * @param {object} props React props.
 * @returns {object} - React component.
 */

function Finder__Announcement(props: Announcement) {
  const clean = DOMPurify.sanitize(props.announcement, {
    ALLOWED_TAGS: ["strong", "p", "h2", "h3", "a"],
  });

  return (
    <div className="finder__results__announcement">
      <p dangerouslySetInnerHTML={{ __html: clean }} />
    </div>
  );
}

Finder__Announcement.propTypes = {
  announcement: PropTypes.string,
};

export default Finder__Announcement;
