import React from "react";
import { createRoot } from "react-dom/client";
import Finder from "../../shared/forms/finder/finder";
import { finderConfig } from "../../shared/logic/funnelback";

document.addEventListener("DOMContentLoaded", () => {
  if (/(Trident|MSIE)/.test(navigator.userAgent)) {
    return;
  } else {
    const finders = document.querySelectorAll<HTMLElement>(".wrapper--finder");
    if (finders) {
      Array.from(finders).forEach((finder) => {
        if (finder.dataset.config) {
          finderConfig(finder.dataset.config).then((config) => {
            const root = createRoot(finder);

            root.render(<Finder config={config} element={finder} />);
          });
        }
      });
    }
  }
});
