import React from "react";
import PropTypes from "prop-types";
import { ResultsCaseStudy } from "../../types/cards/card";

/**
 * Render a Funnelback result as a case study card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__CaseStudy(props: ResultsCaseStudy) {
  const subtitle =
      props.details.listMetadata && props.details.listMetadata.status ? (
        <p className="card__type">{props.details.listMetadata.status[0]}</p>
      ) : (
        props.details.listMetadata &&
        props.details.listMetadata.type && (
          <p className="card__type">{props.details.listMetadata.type[0]}</p>
        )
      ),
    school = props.details.listMetadata &&
      props.details.listMetadata.school && (
        <p className="card__tag">
          <span
            className="fas fa-fw fa-sharp fa-university icon"
            aria-hidden="true"
          />{" "}
          <span>School:</span>{" "}
          <span>
            {props.details.listMetadata.school.length > 2
              ? props.details.listMetadata.school.slice(0, -1).join(", ") +
                ", and " +
                props.details.listMetadata.school.slice(-1)
              : props.details.listMetadata.school.join(", and ")}
          </span>
        </p>
      ),
    department = props.details.listMetadata &&
      props.details.listMetadata.department && (
        <p className="card__tag">
          <span
            className="fas fa-fw fa-sharp fa-building icon"
            aria-hidden="true"
          />{" "}
          <span>Department:</span>{" "}
          <span>
            {props.details.listMetadata.department.length > 2
              ? props.details.listMetadata.department.slice(0, -1).join(", ") +
                ", and " +
                props.details.listMetadata.department.slice(-1)
              : props.details.listMetadata.department.join(", and ")}
          </span>
        </p>
      ),
    centre = props.details.listMetadata &&
      props.details.listMetadata.centre && (
        <p className="card__tag">
          <span className="fas fa-fw fa-vial icon" aria-hidden="true" />{" "}
          <span>Research centre:</span>{" "}
          <span>
            {props.details.listMetadata.centre.length > 2
              ? props.details.listMetadata.centre.slice(0, -1).join(", ") +
                ", and " +
                props.details.listMetadata.centre.slice(-1)
              : props.details.listMetadata.centre.join(", and ")}
          </span>
        </p>
      );

  return (
    <li className="card card--casestudy">
      <a href={props.details.liveUrl} className="card__anchor card__details">
        <div className="card__details__text">
          <h3 className="card__heading">{props.details.title}</h3>
          {subtitle}
          <p className="card__description">
            {props.details.listMetadata &&
              props.details.listMetadata.c &&
              props.details.listMetadata.c[0]}
          </p>
          {school}
          {department}
          {centre}
        </div>
      </a>
    </li>
  );
}

Finder__Results__CaseStudy.propTypes = {
  details: PropTypes.object,
};

export default Finder__Results__CaseStudy;
