import React from "react";
import PropTypes from "prop-types";
import type { Select } from "../../types/filters/filters";

function Finder__Select(props: Select) {
  const randomNumber = props.mobile ? "mobile" : "desktop",
    currentValue = (props.facet && props.query.facets[props.facet.meta]) || "";

  // reduce the facet configuration to an array of all possible values for
  // the facet
  const allFacets =
    props.facet &&
    props.facet.values &&
    props.facet.values.reduce((acc, data) => {
      return [...acc, data];
    }, []);

  // reduce the Funnelback response for the facet to an array of valid
  // values for the current query
  const responseFacets =
    props.responseFacet[0] && props.responseFacet[0].allValues
      ? props.responseFacet[0].allValues.reduce((acc, data) => {
          return [...acc, data.data];
        }, [])
      : [];

  // count how many possible facets are not valid for the current query
  // and will be hidden
  const hiddenFacets: number = allFacets
    ? allFacets
        .map((facet) => facet.data)
        .filter((facet) => responseFacets.indexOf(facet.toLowerCase()) < 0)
        .length
    : 0;

  const setFacet = (value: string) => {
    const newQuery = {
      ...props.query,
      facets: { ...props.query.facets },
    };

    if (props.dependencies) {
      props.dependencies.forEach((facet) => {
        delete newQuery.facets[facet.meta];
      });
    }


    if (value) {
      newQuery.facets[props.facet.meta] = value;
    } else{
      delete newQuery.facets[props.facet.meta];
    }


    newQuery.startRank = 1;
    newQuery.misspelling = null;
    newQuery.interacted = true;

    props.update.query(newQuery);
    props.update.results(!props.update.updateState);
  };

  if (
    props.facet &&
    props.facet.values &&
    props.facet.values.length > hiddenFacets
  ) {
    return (
      <div
        className={`finder__filter finder__select ${
          currentValue && "finder__select--selected"
        }`}
      >
        <label htmlFor={`meta_${props.facet.meta}_sand--${randomNumber}`}>
          {props.facet.name}
        </label>
        <select
          name={`meta_${props.facet.meta}_sand`}
          id={`meta_${props.facet.meta}_sand--${randomNumber}`}
          onChange={(e) => setFacet(e.target.value)}
          value={currentValue}
        >
          <option value="" id={`meta${props.facet.meta}all--${randomNumber}`}>
            {props.facet.noSelection}
          </option>

          {props.facet.values.map((value, i) => {
            const responseFacetDetails =
              props.responseFacet[0] &&
              props.responseFacet[0].allValues &&
              props.responseFacet[0].allValues.filter(
                (responseFacetValue) =>
                  responseFacetValue.data.toLowerCase() ===
                  value.data.toLowerCase(),
              );
            if (
              currentValue.toLowerCase() === value.data.toLowerCase() ||
              (responseFacetDetails && responseFacetDetails[0])
            ) {
              return (
                <option key={i} value={value.data.toLowerCase()}>
                  {value.label}
                  {currentValue !== value.data &&
                  responseFacetDetails[0] &&
                  responseFacetDetails[0].count > 0
                    ? ` (${responseFacetDetails[0].count})`
                    : ""}
                </option>
              );
            } else {
              return null;
            }
          })}
        </select>
      </div>
    );
  } else {
    return null;
  }
}

Finder__Select.propTypes = {
  facet: PropTypes.object,
  query: PropTypes.object,
  responseFacet: PropTypes.arrayOf(PropTypes.object),
  update: PropTypes.object,
  dependencies: PropTypes.arrayOf(PropTypes.object),
  mobile: PropTypes.bool,
};

export default Finder__Select;
