import { isURLSearchParams } from "./url-params";
import type { FacetDetails } from "../types/facet";

/**
 * Preserve the search state in the URL parameters.
 *
 * @param {string} currQuery The search query.
 * @param {integer} currStartRank The start rank.
 * @param {object[]} currFacets A map of facet meta labels to their values.
 * @param {*} facetLabels Array of facet definitions.
 */
export default function replaceHistory(
  currQuery: string,
  currStartRank: number,
  currFacets: { [key: string]: string },
  currParameters: { [key: string]: string },
  currSort: string,
  facetLabels: FacetDetails[],
  defaultSort: string,
  hasMounted: boolean,
) {
  if (hasMounted) {
    const params = new URLSearchParams(window.location.search);

    if (isURLSearchParams(params)) {
      if (currQuery !== "") {
        params.set("query", currQuery);
      } else {
        params.delete("query");
      }

      if (currStartRank && currStartRank !== 1) {
        params.set("start_rank", currStartRank.toString());
      } else {
        params.delete("start_rank");
      }

      if (currSort !== defaultSort && currSort !== "") {
        params.set("sort", currSort);
      } else {
        params.delete("sort");
      }

      facetLabels.forEach((facet) => {
        if (currFacets[facet.meta]) {
          params.set(`meta_${facet.meta}_sand`, currFacets[facet.meta]);
        } else {
          params.delete(`meta_${facet.meta}_sand`);
        }
      });

      facetLabels.forEach((facet) => {
        if (currParameters[facet.meta]) {
          params.set(facet.meta, currParameters[facet.meta]);
        } else {
          params.delete(facet.meta);
        }
      });

      const hasParams = params.toString().length ? "?" : "";

      window.history.replaceState(
        {},
        "",
        `${window.location.pathname}${hasParams}${params.toString()}`,
      );
    }
  }
}
