import Accommodation from "./finder__results__accommodation";
import CaseStudy from "./finder__results__casestudy";
import Centre from "./finder__results__centre";
import Contact from "./finder__results__contact";
import Course from "./finder__results__course";
import Event from "./finder__results__event";
import Funding from "./finder__results__funding";
import Generic from "./finder__results__generic";
import Module from "./finder__results__module";
import News from "./finder__results__news";
import Profile from "./finder__results__profile";
import PropTypes from "prop-types";
import React from "react";
import Research from "./finder__results__research";
import type { Card } from "../../types/cards/card";

/**
 * Render a Funnelback result as the appropriate card type.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Card(props: Card) {
  switch (props.type) {
    case "accommodation":
      return <Accommodation details={props.details} />;
    case "casestudy":
      return <CaseStudy details={props.details} />;
    case "centre":
      return <Centre details={props.details} />;
    case "contact":
      return <Contact details={props.details} />;
    case "course":
      return <Course details={props.details} query={props.query} />;
    case "event":
      return <Event details={props.details} />;
    case "funding":
      return <Funding details={props.details} query={props.query} />;
    case "module":
      return <Module details={props.details} query={props.query} />;
    case "news":
      return <News details={props.details} />;
    case "profile":
      return <Profile details={props.details} query={props.query} />;
    case "research":
      return <Research details={props.details} query={props.query} />;
    default:
      return <Generic details={props.details} />;
  }
}

Finder__Results__Card.propTypes = {
  details: PropTypes.object,
  query: PropTypes.object,
  type: PropTypes.string,
};

export default Finder__Results__Card;
