/**
 * @module patterns/finder/components/cards/finder__results__course
 * @author Web Development
 * @copyright City, University of London 2019
 */

import PropTypes from "prop-types";
import React from "react";
import formatLabel from "../results/formatLabel";
import { uppercaseFirstLetterLowercaseRest } from "../../util";
import type { ResultsCourse } from "../../types/cards/card";

/**
 * Render a Funnelback result as a course card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */

const externalSiteUrl = ["www.citystgeorges.ac.uk"];
function Finder__Results__Course(props: ResultsCourse) {
  const subtitle =
      (props.details.listMetadata &&
        props.details.listMetadata.type &&
        props.details.listMetadata.type[0]) ||
      (props.details.listMetadata &&
        props.details.listMetadata.level &&
        props.details.listMetadata.level[0]) ||
      null,
    award = props.details.listMetadata &&
      props.details.listMetadata.qualification && (
        <p className="card__tag">
          <span className="fas fa-fw fa-award icon" aria-hidden="true" />{" "}
          <span>
            Award
            {props.details.listMetadata.qualification.length > 1 && "s"}:
          </span>{" "}
          <span>{props.details.listMetadata.qualification.join(", ")}</span>
        </p>
      ),
    duration = props.details.listMetadata &&
      props.details.listMetadata.duration && (
        <p className="card__tag">
          <span className="fal fa-fw fa-clock icon" aria-hidden="true" />{" "}
          <span>Duration:</span>{" "}
          <span>{props.details.listMetadata.duration[0]}</span>
        </p>
      ),
    method = props.details.listMetadata &&
      props.details.listMetadata.method &&
      props.details.listMetadata.level &&
      props.details.listMetadata.level[0] !==
        "Short courses and professional development" &&
      ((props.query.facets && props.query.facets.method) ||
        props.query.query.indexOf(props.details.listMetadata.method[0]) >= 0 ||
        props.details.listMetadata.method[0].indexOf("Online") >= 0) && (
        <p className="card__tag">
          <span
            className="fa-solid fa-book-open-reader icon"
            aria-hidden="true"
          />{" "}
          <span>
            Method
            {props.details.listMetadata.method.length > 1 && "s"} of study:
          </span>{" "}
          <span>
            {uppercaseFirstLetterLowercaseRest(
              Array.from(new Set(props.details.listMetadata.method)).join("|"), // Remove any duplicates & format
            )
              .split("|")
              .join(", ")}
          </span>
        </p>
      ),
    location =
      props.details.listMetadata &&
      String(props.details.listMetadata.location).toLowerCase() !== "tooting"
        ? props.details.listMetadata.location &&
          props.query.facets &&
          props.query.facets.location && (
            <p className="card__tag">
              <span
                className="fas fa-fw fa-map-marker-alt icon"
                aria-hidden="true"
              />{" "}
              <span>Location:</span>{" "}
              <span>
                {/* Avoid duplication of 'Online' location label if location and method of study are both online */}
                {props.details.listMetadata.location.join("|") ===
                "Online|Online"
                  ? "Online"
                  : props.details.listMetadata.location.join(", ")}
              </span>
            </p>
          )
        : null,
    school = props.details.listMetadata &&
      props.details.listMetadata.school && (
        <p className="card__tag">
          <span
            className="fas fa-fw fa-sharp fa-university icon"
            aria-hidden="true"
          />{" "}
          <span>School:</span>{" "}
          <span>
            {props.details.listMetadata.school.map((e, i) =>
              i === 0 ? (
                <React.Fragment key={i}>{e.replace("and", "&")}</React.Fragment>
              ) : (
                <React.Fragment key={i}>| {e}</React.Fragment>
              ),
            )}
          </span>
        </p>
      ),
    // City St George's label temporarily in place according to branding guidelines
    stGeorgesLabel =
      props.details.listMetadata &&
      String(props.details.listMetadata.location).toLowerCase() ===
        "tooting" ? (
        <div className="card__label card--course__label--stgeorges">
          <p>
            <span
              className="fas fa-fw fa-map-marker-alt icon"
              aria-hidden="true"
            />{" "}
            Tooting Campus
          </p>
        </div>
      ) : null,
    // City and Business School use different metadata values; check for both
    clearing = props.details.listMetadata &&
      props.details.listMetadata.clearing &&
      ["Yes", "yes"].includes(props.details.listMetadata.clearing[0]) && (
        <div className="card__label card--course__label--clearing">
          <p className="card__label card--course__label--clearing">Clearing</p>
        </div>
      ),
    external = !externalSiteUrl.some(
      (s) => props.details.indexUrl && props.details.indexUrl.indexOf(s) >= 0,
    ) ? (
      <>
        {" "}
        <span
          className="fas fa-sharp fa-external-link icon"
          aria-label="(external link)"
        ></span>
      </>
    ) : null,
    courseCode =
      props.details.listMetadata &&
      props.details.listMetadata.code &&
      ([
        "Undergraduate degree",
        "Foundation course",
        "Postgraduate taught degree",
      ].some(
        (s) =>
          props.details.listMetadata &&
          props.details.listMetadata.level &&
          props.details.listMetadata.level.indexOf(s) >= 0,
      ) ||
        props.query.query.indexOf(props.details.listMetadata.code[0]) >= 0) ? (
        <p className="card__tag">
          <span
            className="fas fa-fw fa-graduation-cap icon"
            aria-hidden="true"
          />{" "}
          <span>
            Course code
            {props.details.listMetadata.code.length > 1 && "s"}:
          </span>{" "}
          <span>{props.details.listMetadata.code.join("/")}</span>
        </p>
      ) : null,
    entryYears =
      props.details.listMetadata &&
      props.details.listMetadata.entryyears &&
      (["Undergraduate degree"].some(
        (s) =>
          props.details.listMetadata &&
          props.details.listMetadata.level &&
          props.details.listMetadata.level.indexOf(s) >= 0,
      ) ||
        props.query.query.indexOf(props.details.listMetadata.entryyears[0]) >=
          0) ? (
        <p className="card__tag">
          <span className="fas fa-fw fa-calendar icon" aria-hidden="true" />{" "}
          <span>
            Year
            {props.details.listMetadata.entryyears.length > 1 && "s"} of entry:
          </span>{" "}
          <span>{props.details.listMetadata.entryyears.join("/")}</span>
        </p>
      ) : null,
    description = props.details.listMetadata &&
      props.details.listMetadata.c &&
      props.details.listMetadata.c[0] && (
        <p className="card__description">
          {props.query.query
            ? formatLabel(props.details.listMetadata.c[0], props.query.query)
            : props.details.listMetadata.c[0]}
        </p>
      );

  return (
    <li className="card card--course">
      <a href={props.details.liveUrl} className="card__anchor card__details">
        <div className="wrapper--card__label__details">
          {clearing}
          <div className="card__details__text">
            <h3 className="card__heading">
              {props.query.query
                ? formatLabel(props.details.title, props.query.query)
                : props.details.title}
              {external}
            </h3>
            <p className="card__type">
              {props.query.query && subtitle
                ? formatLabel(subtitle, props.query.query)
                : subtitle && subtitle}
            </p>
            {description}
            {stGeorgesLabel}
            {school}
            {award}
            {duration}
            {courseCode}
            {entryYears}
            {method}
            {location}
          </div>
        </div>
      </a>
    </li>
  );
}

Finder__Results__Course.propTypes = {
  details: PropTypes.object,
  query: PropTypes.object,
};

export default Finder__Results__Course;
