import React from "react";

const formatLabel = (label: string, value: string) => {
  if (!value) return label;

  const parts = label.split(value);

  return (
    <React.Fragment>
      {parts.reduce<React.ReactNode[]>((acc, part, i) => {
        if (i === 0) {
          return [part];
        }
        return acc.concat(
          <span className="highlightText" key={value + part}>
            {value}
          </span>,
          part,
        );
      }, [])}
    </React.Fragment>
  );
};

export default formatLabel;
